.table {
    width: 90%;
    margin: auto;
  }
  
  /* HEADER */
  /* FILTER OPTIONS */
  .table-header {
    display: flex;
    align-items: center;
  }
  
  .table-header > * {
    flex: 1 1 0;
  }
  
  .table-header--entries select {
    cursor: pointer;
    margin: 0.25rem;
    padding: 0.25rem;
    border-radius: 0.25rem;
    border: 1px solid var(--primary);
    color: var(--primary);
    font-size: small;
  }
  
  .table-header--search {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .table-header--search img {
    width: 16px;
    height: 16px;
    margin-right: 0.25rem;
  }
  
  .table-header--search input {
    margin-left: 0.5rem;
    padding: 0.25rem;
    border: 1px solid var(--primary);
    border-radius: 0.25rem;
  }
  
  /* MAIN  */
  /* EMPLOYEES LIST TABLE */
  .table-main {
    margin: 2rem auto;
    border-radius: 0.5rem;
    box-shadow: rgba(149, 157, 165, 0.3) 0px 0px 2.5rem 0.5px !important;
  }
  
  .table-main--list {
    table-layout: auto;
    width: 100%;
    border-color: var(--dark);
  }
  
  .table-main--list thead :first-child :first-child {
    border-top-left-radius: 0.5rem;
  }
  
  .table-main--list thead :first-child :last-child {
    border-top-right-radius: 0.5rem;
  }
  
  .table-main--list tbody :last-child :first-child {
    border-bottom-left-radius: 0.5rem;
  }
  
  .table-main--list tbody :last-child :last-child {
    border-bottom-right-radius: 0.5rem;
  }
  
  .table-main--list th {
    padding: 0.25rem; 
    background-color: var(--primary);
    color: var(--light);
  }
  
  .table-main--list thead :nth-child(2) > * {
    padding-bottom: 0.5rem;
    font-family: 'Open Sans Condensed';
    color: var(--dark);
  }
  
  .table-main--list thead :nth-child(1) .table-main--sorter {
    display: none;
  }
  
  .table-main--sorter {
    padding-left: 0.25rem;
  }
  
  .table-main--bullet,
  .table-main--arrowUp,
  .table-main--arrowDown {
    padding: 0;
    background-color: transparent;
    color: var(--dark);
    width: 1rem;
    height: 1rem;
  }
  
  .table-main--bullet:hover,
  .table-main--bullet:focus,
  .table-main--bullet:active,
  .table-main--arrowUp:hover,
  .table-main--arrowUp:focus,
  .table-main--arrowUp:active,
  .table-main--arrowDown:hover,
  .table-main--arrowDown:focus,
  .table-main--arrowDown:active {
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    color: var(--light) !important;
  }
  
  .table-main--list tbody {
    background-color: white;
  }
  
  /* even = 1/2 row */
  .table-main--list tr:nth-child(even) {
    background-color: var(--disabled);
  }
  
  .table-main--list tr:hover,
  .table-main--list tr:focus {
    background-color: var(--tertiary);
  }
  
  /* Id input */
  .table-main--list tbody tr :nth-child(1) {
    text-align: right;
  }
  
  /* Birth Date input */
  .table-main--list tbody tr :nth-child(4),
  /* State input */
  .table-main--list tbody tr :nth-child(7),
  /* Zip Code */
  .table-main--list tbody tr :nth-child(8),
  /* Start Date input */
  .table-main--list tbody tr :nth-child(9) {
    text-align: center;
  }
  
  .table-main--list td {
    padding: 0.25rem 0.5rem;
  }
  
  /* FOOTER */
  /* TABLE NAVIGATION */
  .table-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
  }
  
  .table-footer--pageIndex {
    padding: 0.25rem;
    border-radius: 0.25rem;
    color: var(--dark);
  }
  
  .table-nav--btn {
    cursor: pointer;
    margin: 0.25rem;
    padding: 0.25rem 0.5rem;
    background-color: var(--tertiary);
    border-radius: 0.25rem;
    border: 1px solid var(--primary);
    color: var(--dark);
    line-height: 1.25rem;
  }
  
  .table-nav--btn:hover {
    background-color: var(--tertiary-constrast);
  }
  
  .table-nav--btn:disabled {
    background-color: var(--disabled);
  }
  
  @media screen and (max-width: 1260px) {
    .table-main--sorter {
      padding-left: 0;
      display: block;
    }
  }
  
  /* ADAPTATIVE */
  @media screen and (max-width: 1260px) {
    .table-main--sorter {
      padding-left: 0;
      display: block;
    }
  }
  
  @media screen and (max-width: 968px) {
    .table {
      width: 100%;
    }
  
    .table-main {
      border-radius: 0;
      overflow-x: scroll;
      overflow-y: hidden;
    }
  
    /* corners */
    .table-main--list thead :first-child :first-child {
      border-top-left-radius: 0;
    }
  
    .table-main--list thead :first-child :last-child {
      border-top-right-radius: 0;
    }
  
    .table-main--list tbody :last-child :first-child {
      border-bottom-left-radius: 0;
    }
  
    .table-main--list tbody :last-child :last-child {
      border-bottom-right-radius: 0;
    }
    /* end corners */
  
    .table-main--list {
      width: 100%;
      border-radius: 0;
      border-collapse: separate;
      background-color: transparent;
      font-size: small;
    }
  
    .table-main::-webkit-scrollbar {
      width: 5px;
      height: 5px;
    }
  
    .table-main::-webkit-scrollbar-track {
      background-color: transparent;
    }
  
    .table-main::-webkit-scrollbar-thumb {
      background-color: var(--tertiary) !important;
    }
  
    .table-footer {
      width: 90%;
      margin: 0 auto 2rem;
    }
  }
  
  @media screen and (max-width: 768px) {
    .table-header {
      flex-direction: column;
      height: 125px;
    }
  
    .table-footer {
      flex-direction: column;
    }
  }
  
  @media screen and (max-width: 375px) {
    .table-footer--nav {
      display: flex;
    }
  }